
<template>
<section>
    <tg-list v-show="IsMapConfigView" :IsShowAction="true" ModuleName="Ticketing" SubModuleCode="FSM_MapExtend"
    :LegendArray="[]" :widgets="[]" 
            IdentityColumn="ID" :HeaderText="$t('MapExtend')"  :listType="['List']" :ListData="mapList"
            :HeaderData="Headers"  :HeaderButtons="listheaderbuttons" :TotalRecords="TotalRecords"
            :ListDataCallBackFunction="fetchMapConfigList" 
            :showCheckBox="false" :SortExp="SortExp"  :SortBy="SortBy"   v-on:SortdataButtonClick="sortdata"
            :NorecordfoundText="$t('NoRecordfound')" :RenderRowActionMethod="renderActions"
            v-on:PagerButtonClick="pagerMethod" v-on:ActionButtonClick="actionButtonClick" v-on:HeaderButtonClick="commonHeaderButtonClick"
            > <template v-slot:slotdata="{data}">
                <template v-if="data.column.COLUMN_NAME == 'status_id'">
                    <td class="text-center single-action tourguide_a">
                        <span v-bind:class="getStatusClass(data.row.STATUS_ID)">
                            <a href="javascript:;" title="" class="" data-toggle-tooltip="tooltip" data-original-title="Active">
                                <em v-on:click="changeMapStatus(data,'INACTIVE')" v-if="data.row[data.column.COLUMN_NAME]==1001" class="fa fa-check text-success action-icon"></em>
                                <em v-on:click="changeMapStatus(data,'ACTIVE')" v-if="data.row[data.column.COLUMN_NAME]==1002" class="fa fa-close text-danger action-icon"></em>
                            </a>
                        </span>
                    </td>
                    
                </template>
                <template v-if="data.column.COLUMN_NAME == 'created_at'">
                    <td class="text-center single-action tourguide_a">
                       {{ $options.filters.formatDate(data.row.created_at) }}
                    </td>
                    
                </template>
             </template></tg-list>
            <ManageMap v-if='ShowManageMap==true' :EditData="EditData"></ManageMap>
</section>
</template>
<script>
import Schema from "@/views/MapConfig/MapConfigSchema";
import DataServices from "@/services/DataService";
import ManageMap from './../../components/Modal/modalManageMap.vue'
import SearchFilter from '../../components/Common/search-filter.vue';
export default{
    components:{
        ManageMap,
        SearchFilter
    },
    data() {
        return {
            MapId: null,
            EditData:null,
            Headers: [ { 
             "COLUMN_NAME": "title",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Title",
                "DISPLAY_ORDER": 1
             },
            {
                "COLUMN_NAME": "api_key",
                "DATA_TYPE": "text",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "API Key",
                "DISPLAY_ORDER": 2,
            },
            {
                "COLUMN_NAME": "type",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Mode Type",
                "DISPLAY_ORDER ": 3,
            },
            {

                "COLUMN_NAME": "created_at",
                "DATA_TYPE": "string",
                "SORTABLE": true,
                "VISIBLE": true,
                "DISPLAY_NAME": "Created At",
                "DISPLAY_ORDER": 4,

            },
            {

                "COLUMN_NAME": "status_id",
                "DATA_TYPE": "string",
                "SORTABLE": false,
                "VISIBLE": true,
                "DISPLAY_NAME": "Status",
                "DISPLAY_ORDER": 5,

            }],
            TotalRecords: 0,
            PageNumber: 1,
            PageSize: 10,
            SortBy: '',
            SortExp: '',
            searchKeyWord: '',
            mapList: [],
            ItemObject: null,
            showaddNewModal: false,
            noRecord: false,
            ShowManageMap:false,
            listheaderbuttons: Schema.buttons,
            listActions: Schema.listActions,
            IsMapConfigView:true,
            modalItem:''
        }
    },
    created: function () {
        
     this.fetchMapConfigList();
     setTimeout(() => {
        $(".theme-primary").prepend('<span class="p-name text-white">Map Accounts</span>')
      }, 500);
    },
    methods:{
        commonHeaderButtonClick: function (event) { 
            switch (event.callbackfunction) {  
                case "openadd":
                    this.openaddTicket();
                    break;
                case "openaddMap":
                    this.openaddMaps();
                    break;
            }
        },
        openaddMaps:function(){
            var vm=this;
            vm.ShowManageMap=true;
            this.EditData= '';
        },
        renderActions:function(){
             var self = this;
            let rtnActionArray = [];
            self.listActions.forEach(actionItem => {
                let action = { ...actionItem };
                switch (action.title) {
                    case 'Add New':
                        action.isVisible = true;
                        break;
                    default: {
                        action.isVisible = true;
                    }
                }
                rtnActionArray.push(action);
            });
            return rtnActionArray;
        },
        actionButtonClick: function (callbackfunction, item, index, event) {
            
            switch (callbackfunction) {
                case 'openshowaddNewModal':
                    this.openshowaddNewModal(item.map_id);
                    break;
                case 'deleteMap':
                    this.deleteMap(item.map_id);
                 break;
                


            }
        },
        Close:function(){
            var vm=this;
            vm.ShowManageMap=false;
        },
        pagerMethod: function (value) {
            this.PageSize = value.PageSize;
            this.PageNumber = value.PageNumber;
            this.fetchMapConfigList();
        },
        fetchMapConfigList: function () {            
            var vm = this;
            var url = `sortBy=${vm.SortBy}&sortExp=${vm.SortExp}&pageSize=${vm.PageSize}&pageNumber=${vm.PageNumber}&searchKeyWord=${vm.searchKeyWord}`;
            DataServices.MapList(url).then(function (response) {
                if (response.data.result != null) {
                    if(JSON.parse(response.data.result).data.length>0){
                        vm.TotalRecords=JSON.parse(response.data.result).data[0].total_records;
                    }                    
                    vm.Headers.forEach((column) => {
                    column.settings = null;
                    if(column.COLUMN_NAME == 'Edit' || column.COLUMN_NAME == 'Delete' ||column.COLUMN_NAME == 'status_id' || column.COLUMN_NAME == 'created_at'){
                        column.settings = {
                            isInSlot : true
                        }
                    }
                });
                vm.mapList = JSON.parse(response.data.result).data;
                    if (vm.mapList.length > 0) {
                        vm.noRecord = false;
                    }
                    else {
                        vm.noRecord = true;
                    }                    
                    setTimeout(function () { vm.ResponsiveDataTable('tablelistingdata') }, 500);
                }

            });
        },
        openshowaddNewModal: function (mapId) {
            this.ShowManageMap = true;
            this.modalItem = mapId;
            this.EditData= mapId;
        },
        sortdata: function (sortBy) {
            this.SortBy = sortBy;
            var sortExp = 'ASC';
            if (this.SortExp == 'ASC') {
                sortExp = 'DESC';
            }
            this.SortExp = sortExp;
            this.SortBy = sortBy;
            this.SortExp = sortExp;
            this.PageNumber = 1;
            this.GroupStart = this.PageNumber;
            this.GroupEnd = this.PageNumber + 2;
            this.fetchMapConfigList();
        },
        deleteMap: function (data) {
            var vm = this;
            vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("Map") + ' ' + vm.$t("Setting")), function () {
                var VarObject = {
                    MapId: data,
                    QueryType: 'DEL'
                };
                var objectPostString = JSON.stringify(VarObject)
                DataServices.SaveMap(objectPostString).then(response => {
                    if (response.data.status == "Success") {
                        //vm.ShowAlert(vm.$t('DeletedSuccess'), vm.$t('Record')), "success", true, vm.$t("Alert");
                        vm.ShowAlert(vm.$t('DeletedSuccess', [vm.$t('Record')]), "Success", true, vm.$t('Alert'));
                        vm.fetchMapConfigList();
                    }
                });
            });

        },
       changeMapStatus: function (item, status) {
            var vm = this;
            var VarObject = {
                MapId: item.row.map_id,
                StatusForUpdate: status,
                QueryType: 'UPDSTS'
            }
            var objectPostString = JSON.stringify(VarObject)
             vm.confirmR(vm.$t("UpdateTicketRouteStatus"), true, vm.$t('Active/Inactive'), function (result) {
            DataServices.SaveMap(objectPostString).then((response) => {
                    if (response.data.status != null) {
                        if (response.data.status === 'Success') {
                            vm.ShowAlert(vm.$t("RecordUpdatedSuccessfully"), "success", true, vm.$t("Alert"));
                            vm.fetchMapConfigList();
                        } else {
                            vm.ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                        }
                    } 
                    else{
                        ShowAlert(vm.$t("UnknownErrorOccur"), "failure", true, vm.$t("Alert"));
                    }
            });
             });
        },
    }
}
</script>