<template>
  <div class="my-popups">
    <loader :is-visible="isLoading"></loader>
    <div class="modal d-block">
      <div class="modal-dialog modal-dialog-centered">
        <div class="modal-content">
          <div class="modal-header theme-primary partition-full d-flex">
            <h4 class="modal-title">{{ $t("ManageMap") }}</h4>
            <button type="button" class="close" v-on:click="Close">
              <em class="fa fa-times" aria-hidden="true"></em>
            </button>
            <span class="user-guide">
              <a class="ancuserguide text-white" data-toggle="collapse" href="#collapseReplyUG" role="button" aria-expanded="false" aria-controls="collapseExample">
                <em class="fa fa-files-o pr-2"></em>User Guide</a>
              <div id="collapseReplyUG" class="divancuserguide collapse overflow-auto">
                <div id="scrollbarreplyuserguideMangeView" class="custom-scrollbar-js">
                  <div class="con" v-html="$t('ManageMapUserGuide')"></div>
                </div>
              </div>
            </span>
          </div>

          <div class="modal-body">
            <Form ref="ProductAssignmentForm">
            <div class="col-md-12">
                <!-- <div class="row">  -->
                  
                    <dynamic-form  v-if="IsLoaded" lang="en" :buttons="buttons" :schema="FormSchema"
                           ></dynamic-form >
                      <div ref="tbleBox" class="listing">
                        <div class="table-responsive tableborder overflow-visible">
                            <table class="table-sorting table table-bordered m-0">
                                <thead class="thead-bg">
                                    <tr>
                                        <th style="min-width: 300px; width: 300px;">
                                            <span>Service Name <i class="text-danger">*</i></span>
                                        </th> 
                                        <th style="min-width: 150px; width: 150px;">
                                            <span>Layer Color</span></th> <th><span>Service Url<i class="text-danger">*</i></span>
                                        </th> 
                                        <th class="px-1 text-center" style="width: 120px;">
                                            <a href="javascript:;" title="Add More" v-on:click="AddServiceCondition()"><i class="fa fa-plus-circle pr-2 fa-size text-success"></i>Add More</a>
                                        </th>
                                    </tr>
                                </thead> 
                                <tbody>
                                    <tr v-for="(item,index) in MapTypeSetting" :key="index">
                                        <td>
                                            <input type="text" class="form-control" placeholder="Enter Service Name" v-validate="'required'" v-model="item.LayerName" />
                                            
                                        </td>
                                        <td class="overflow-visible">
                                            <div class="input-group color-picker">
                                                <Vue2ColorPicker v-bind:color="item.LAYER_COLOR" v-model="item.LayerColor"/>
                                            </div>
                                        </td>
                                        <td>
                                            <input type="text" class="form-control" v-validate="'required'" v-model="item.LayerUrl" />
                                           
                                        </td>
                                        <td class="text-center">
                                            <a href="javascript:;" v-if="index>0" v-on:click="DeleteServiceCondition(index)" title="Delete"><i class="fa fa-trash fa-size text-danger"></i></a>
                                            <a href="javascript:;" v-if="index==0" title="Delete" class="disabled"><i class="fa fa-trash fa-size text-secondary"></i></a>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>  
                        </div><br>
                    </div>
            </div>
            <div class="row flex-row-reverse">
                <div class="col-lg-6 text-right">
                    <div class="col-12 p-0">
                        <a href="javascript:;" tabindex="3" title="Save" v-on:click="SaveMap" class="btn btn-success formbtn"><i class="fa fa-save pr-2"></i> Save</a>&nbsp;
                        <a href="javascript:;" tabindex="4" title="Cancel" v-on:click="Close" class="btn btn-danger formbtn"><i class="fa fa-close pr-2"></i> Cancel</a>
                    </div>
                </div>
                <div class="col-lg-6"><div class="py-2"><span class="mandatory">{{$t('MandatoryString')}}</span></div></div>
            </div>
            </Form>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
import DataServices from "@/services/DataService";
import Vue2ColorPicker from 'vue2-color-picker';
export default {
  components:{
      Vue2ColorPicker
  },
  props:{
    EditData:{
      type:String,
      required:false
    }
  },
  data() {
    return {
      IsLoaded:0,
      isLoading: false,
      ShowDiv:false ,
      ttype: 'Esri',
      ShowTitle:true,
      UtilityData: [],
      MapTypeSetting: [
            {
                Utility: '',
                LayerUrl: '',
                LayerColor:'',
                LayerName: ''
            }
        ],
         FormSchema: [
                {
                    layoutType: "triple",
                    Data: [
                        {
                            astype: "TextField",
                            label: this.$t('Title'),
                            name: "Title",
                            value: "",
                            placeholder: this.$t('Title Name'),
                            validationRules: { "required": true, max: 200 },
                        },
                        {
                            astype: "RadioField",
                            label: this.$t('MapType'),
                            name: "MapType",
                            value: "Esri",                           
                            config:{
                                options:[{name:'Esri',value:'Esri'},{name:'Google',value:'Google'},{name:'MapBox',value:'MapBox'},{name:'LeafLet',value:'LeafLet'}],
                                onChange:this.changeDiv
                            }
                        },
                         {
                            astype: "TextField",
                            label: this.$t('ApiKey'),
                            name: "ApiKey",
                            value: "",
                            placeholder: this.$t('Api Key'),
                            validationRules: { "required": true, max: 200 },
                        },
                    ],
                }  
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                    onClick: this.SaveMap,
                    isVisible:false
                }
            ],
    };
  },
  created: function () {
    this.FetchData();
    this.IsLoaded=1;
  },
  methods: {
    FetchData: function(){
        var vm=this;
        if(vm.EditData!='' && vm.EditData!=null && vm.EditData!=undefined){
                vm.MapTypeSetting=[];
            var url = `MapId=${vm.EditData}`;
            DataServices.MapList(url).then((response) => {
              if(JSON.parse(response.data.result).data[0].type=='Esri'){
                  vm.FormSchema[0].Data[0].value=JSON.parse(response.data.result).data[0].title;
                  vm.FormSchema[0].Data[1].value=JSON.parse(response.data.result).data[0].type;
                  vm.FormSchema[0].Data[2].value=JSON.parse(response.data.result).data[0].api_key;
                  JSON.parse(response.data.result).EsriSetting.forEach(element => {
                        vm.MapTypeSetting.push({
                          Utility: element.UTILITY_ID,
                          LayerUrl: element.LAYER_URL,
                          LayerColor:element.LAYER_COLOR,
                          LayerName: element.LAYER_NAME
                        })
                  });
                  vm.$refs.tbleBox.classList.remove('d-none'); 
              }
              else{
                  vm.FormSchema[0].Data[0].value=JSON.parse(response.data.result).data[0].title;
                  vm.FormSchema[0].Data[1].value=JSON.parse(response.data.result).data[0].type;
                  vm.FormSchema[0].Data[2].value=JSON.parse(response.data.result).data[0].api_key;
                  vm.MapTypeSetting=JSON.parse(response.data.result).EsriSetting;
                  vm.$refs.tbleBox.classList.add('d-none')
                  vm.MapTypeSetting.push({
                          Utility: '',
                          LayerUrl:'',
                          LayerColor:'',
                          LayerName:''
                        })
              }
            })
          }
    },
    changeDiv:function(event,field){
        var vm=this;       
        field.value=event.target.getAttribute('data-value');           
        if(field.value=='Esri')
        {
            vm.ttype='Esri';
            vm.$refs.tbleBox.classList.remove('d-none');            
            vm.ShowTitle=true;
        }
        else if(field.value=='Google'){
            vm.ttype='Google';
            vm.$refs.tbleBox.classList.add('d-none')
        }
        else if(field.value=='MapBox'){
            vm.ttype='MapBox';
            vm.$refs.tbleBox.classList.add('d-none')
        }
        else if(field.value=='LeafLet'){
            vm.ttype='LeafLet';
          
            vm.$refs.tbleBox.classList.add('d-none')
        }
    },
    DeleteServiceCondition: function (index) {
        var vm = this;
        vm.confirmR(vm.$t("ConfirmDelete"), true, vm.$t("Delete"), function () {
            vm.MapTypeSetting.splice(index, 1);
        });
    },
    SaveMap: function () {
      debugger
        var vm = this;
       vm.$refs.ProductAssignmentForm.validate().then(result=>{
        debugger
        if(result){
          var data = {
            Title: vm.FormSchema[0].Data[0].value,
            Apikey: vm.FormSchema[0].Data[2].value,
            Type: vm.FormSchema[0].Data[1].value,
            EsriMapTypeSetting: vm.MapTypeSetting,
            MapId: vm.modalItem,
            QueryType: (parseInt(vm.modalItem) > 0) ? "UPD" : 'INS'
        };
        var objectPostString = JSON.stringify(data)
        DataServices.SaveMap(objectPostString).then((response) => {
                if (response.data.status == "Success") {
                  vm.ShowAlert(vm.$t('DataSavedSuccessfully'), "success", true, vm.$t("Alert"));
                  vm.$parent.fetchMapConfigList();
                  vm.$parent.Close();
                }
        });
        }    
       })       
    },
     AddServiceCondition: function () {
        var length = 0;
        var vm = this;
        if (vm.UtilityData != null && vm.UtilityData.length > 0) {
            length = vm.UtilityData.length;
        }
        if (this.MapTypeSetting.length == length && 1 != 1) {
            var msg = String.Format(Globalize.localize('NotAddMoreMessage'), Globalize.localize('Record'));
            vm.ShowAlert(msg, "warning", true, Globalize.localize("Alert"));
        }
        else {
            var obj = {
                Utility: '',
                LayerName: '',
                LayerColor:'',
                LayerUrl: ''
            };
            vm.MapTypeSetting.push(obj);
        }
      },
      Close:function(){
          var vm=this;
          vm.$parent.Close();
      }
    },
};
</script>